import React from 'react'
import config from '../config';
import {Container} from 'react-bootstrap'

function Education() {
  return (
    <Container className="my-card my-info">
      <p className="card-title">Educational background</p>
      {config?.educations?.map((item) => (
        <div>
          <p className="title">{`${item.from} - ${item.to}`}</p>  
          <p className="title">{item.major}</p>
          <p className="title">{item.department}</p>
          <p className="title">{item.university}</p>
        </div>
      ))}
    </Container>
  )
}

export default Education;
